.wionp{
    &-wraper{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 80px;
        @include max (1399px){
            gap: 60px;
        }
        @include max (1199px){
            gap: 40px;
        }
        @include max (991px){
            grid-template-columns: repeat(1, 1fr);
        }
       
    }
    &-wrap{
        margin-bottom: 80px;
        transition: all 0.4s;
        position: relative;
        @include max (1399px){
            margin-bottom: 60px;
        }
        @include max (1199px){
            margin-bottom: 40px;
        }
         @include max (991px){
            margin-bottom: 30px;
        }
        &:hover{
            .wionp-thumb img{
                transform: scale(1.1);
            }
            .wionp-meta::before{
                width: 100%;
            }
            .wionportfolio-btn{
                background-color: var(--accent-bg);
                border: 1px solid transparent;
                i{
                    transform: translate(42px, -42px);
                    color: var(--heading-color)!important;
                }
                &::before{
                    transform: translate(-24px,18px);
                }
            }
         
        }
        &.wrap2{
            margin-bottom: 0;
        }
        &.wrap3{
            @include max (767px){
                margin-bottom: 30px;
            }
        }
    }
    &-thumb{
        position: relative;
        overflow: hidden;
        margin-bottom: 24px;
        border-radius: 10px;
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            transition: all 0.4s;
            border-radius: 10px;
            transform-origin: left;
        }
    }
    &-title{
        p{
            padding-top: 15px;
            font-size: 20px;
            line-height: 1.5em;
        }
    }
    &-meta{
        padding-bottom: 24px;
        position: relative!important;
        border-bottom: 1px solid rgba(29, 29, 27, 0.1);
        margin-bottom: 24px;
        &::before{
            content: " ";
            left: 0;
            bottom: 0;
            width: 0;
            height: 1px;
            position: absolute;
            transition: all .2s;
            background: var(--heading-color);
        }
        ul{
            li{
                display: inline-block;
                padding-right: 24px;
                position: relative;
                &::before{
                    content: "";
                    position: absolute;
                    width: 7px;
                    height: 7px;
                    top: 10px;
                    right: 5px;
                    transition: all .4s;
                    border-radius: 100px;
                    background-color: var(--heading-bg);
                }
                &:last-child::before{
                    content: none;
                }
                &:last-child{
                    padding-right: 0;
                }
                a{
                    font-size: 18px;
                    line-height: 1.44em;
                    color: var(--heading-color);
                }
            }
        }
    }
    &-content-wrap{
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &-btn{
        text-align: center;
        margin-top: 80px;
        @include max (991px){
            margin-top: 60px;
        }
        @include max (767px){
            margin-top: 40px;
        }
    }
    // end one

    &-image-expand {
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        overflow: hidden;
        gap: 24px;
        position: relative;
        @include max(991px) {
            flex-direction: column;
        }
    }
    &-img-expand-wrap {
        width: 140px;
        min-height: 550px;
        background: center / cover no-repeat;
        align-items: flex-start;
        transition: flex .5s;
        -webkit-transition: flex .5s;
        -moz-transition: flex .5s;
        -o-transition: flex .5s;
        border-radius: 10px;
        overflow: hidden;
        position: relative;
        transition: all 0.6s;
        padding: 30px;
        z-index: 0;
        @include max(991px) {
            width: 100%;
            display: flex;
            min-height: 450px;
            flex-direction: column;
            justify-content: space-between;
        }
        @include max(479px) {
            min-height: 300px;
        }
        &.expand{
            width: 635px;
            @include max(1199px) {
                width: 100%;
            }
            .wionp-expand-text.content-rotate{
                opacity: 0;
            }
            .wionp-expand-text{
                opacity: 1;
            }
            &:hover .wionp-expand-text{
                opacity: 1;
            }
            &:hover .wionp-expand-text.content-rotate{
                opacity: 0;
            }
        }
        &::before{
            content: "";
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            z-index: 1;
            position: absolute;
            background: linear-gradient(180deg, rgba(29, 29, 27, 0) 26.44%, #1D1D1B 100%);
        }
     
    }
    &-expand-icon{
        img{
            transition: all 0.4s;
            object-fit: cover;
            transform: rotate(0);
        }
    }
    &-expand-text {
        z-index: 3;
        position: absolute;
        width: 380px;
        left: 30px;
        opacity: 0;
        bottom: 30px;
        transition: all .4s;
        @include max(991px) {
            opacity: 1;
            position: static;
        }
        @include max(767px) {
            width: 100%;
        }
        &.content-rotate{
            opacity: 1;
            transform: rotate(-90deg);
            left: -115px;
            bottom: 190px;
            @include max(1199px) {
                left: -135px;
            }
            @include max(991px) {
                opacity: 0;
            }
        }
        h3{
            color: var(--white-color);
            transition: all 0.4s;
            margin-bottom: 15px;
        }
        p{
            color: var(--white-color);
            font-size: 20px;
            line-height: 1.5em;
            transition: all 0.4s;
        }
    }
   
    

    // start inner page
    &-wraper3{
        position: sticky;
        background-color: var(--light-bg);
    }
     // start single service
    &-single-wrap {
        max-width: 1075px;
        @include max (767px){
            max-width: 100%;
        }
    }
    &-single-data{
        h3{
            padding-bottom: 15px;
        }
        p{
            font-weight: 400;
            font-size: 28px;
            line-height: 34px;
            @include max(991px) {
                font-size: 24px;
            }
        }
        ul{
             li{
                position: relative;
                padding-left: 24px;
                margin-bottom: 15px;
                &:last-child{
                    margin-bottom: 0;
                }
                &::before{
                    content: "";
                    position: absolute;
                    width: 10px;
                    height: 10px;
                    top: 10px;
                    left: 0;
                    border-radius: 100px;
                    background-color: var(--heading-bg);
                }
                h3{
                    padding-bottom: 0;
                }
            }
        }
    }
    &-single-thumb{
        padding-bottom: 40px;
        @include max (767px){
            padding-bottom: 30px;
        }
        img{
            border-radius: 10px;
        }
    }
    &-d-thumb{
        img{
            border-radius: 10px;
        }
    }
    &-d-data-text{
        ul{
            display: flex;
            align-items: center;
            flex-wrap: wrap;
            gap: 15px;
            justify-content: space-between;
          
            li{
                font-size: 18px;
                line-height: 1.44em;
     
                span{
                    font-weight: 700;
                    padding-right: 15px;
                }
            }
        }
    }
}
.wionportfolio-slider-js-init{
    .wionp-wrap{
        margin: 0 12px;
    }
}

// add animation




.one {
    top: 130px;
    @include max (991px){
        margin-top: 40px;
    }
    @include max (767px){
        margin-top: 30px;
    }
}

.two {
    top: 80px;
}
.three {
    top: 110px;
}
.four {
    top: 140px;
}
.five {
    top: 120px;
}
.six {
    top: 110px;
}


.card-sticky{
    top: 100px;
    position: sticky;
    background-color: var(--light-bg);
    @include max(991px) {
        position: static;
    }
}