input[type="text"],
input[type="email"],
input[type="password"],
input[type="url"],
input[type="tel"],
input[type="time"],
input[type="date"],
input[type="search"],
input, textarea {
  margin: 0;
  width: 100%;
  height: 60px;
  color:var(--heading-color);
  border-radius: 50px;
  background-color: transparent;
  padding: 17px 24px ;
  border: 1px solid #1D1D1B80;
  transition: all 0.4s;
  &::placeholder{
      color: #1D1D1B80;
   }
}
textarea {
  height: 200px;
  resize: none;
  border-radius: 20px;
  &::placeholder{
   color: #1D1D1B80!important;
   }
  @include max(767px) {
    height: 100px;
  }
}
.wionmain{
   &-item{
      display: flex;
      align-items: flex-end;
      gap: 24px;      
   }
   &-field{
      margin-bottom: 24px;
      h6{
         padding-bottom: 15px;
      }
      input{
         width: 100%;
      }
   }
   &-field-textarea{
      
      h6{
         padding-bottom: 15px;
      }
   }
}
.wionsearch-box {
   position: relative;
   input{
      border: 1px solid var(--heading-color);
      padding: 15px 60px 15px 24px;
      &::placeholder{
         color: var(--heading-color);
      }
   }
}

#wionsearch-btn{
  padding: 17px;
  border: 1px solid transparent;
  transition: all 0.4s;
  background-color: var(--accent-bg);
  position: absolute!important;
  right: 5px;
  top: 5px;
}

.mt-34{
   margin-top: 34px;
   @include max (991px){
      margin-top: 24px;
   }
}