.wiont{
    &-section{
        border-radius: 10px;
        padding: 130px 0 210px;
        @include max (991px){
            padding: 80px 0 170px;
        }
        @include max (767px){
            padding: 60px 0 130px;
        }
    }
    &-wrap{
        padding: 30px;
        margin: 0 15px;
        border-radius: 10px;
        background-color: var(--white-bg);
    }
    &-author-wrap{
        display: flex;
        align-items: center;
        gap: 12px;
    }
    &-author-thumb{
        img{
            border-radius: 100px;
        }
    }
    &-author-data{
        h6{
            font-size: 20px;
            line-height: 1.33em;
            padding-bottom: 5px;
        }
        p{
            font-weight: 400;
            font-size: 18px;
            line-height: 1.44em;
        }
    }
    // end one
    &-wrap2{
        max-width: 830px;
        text-align: center;
        margin: 0 auto;
    }
    &-author-wrap2{
        padding-top: 50px;
        padding-bottom: 80px;
        @include max (991px){
            padding-top: 40px;
            padding-bottom: 60px;
        }
        @include max (767px){
            padding-top: 30px;
            padding-bottom: 50px;
        }
    }
    &-author-thumb2{
        padding-bottom: 15px;
        img{
            border-radius: 100px;
            display: inline-block;
        }
    }
    // end two
    &-wrap3{
        padding: 30px;
        background-color: #fff;
        border-radius: 10px;
        max-width: 1076px;
        margin: 0 auto;
    }
    &-thumb3{
        img{
            width: 100%;
            border-radius: 10px;
        }
    }
    &-content3{
        padding-left: 30px;
        @include max (1199px){
            padding-left: 20px;
        }
        @include max (991px){
            padding-left: 0;
            padding-top: 40px;
        }
        @include max (767px){
            padding-top: 30px;
        }
    }
    &-author-wrap3{
        display: flex;
        align-items: end;
        justify-content: space-between;
    }
    
}



.mCSB_dragger{
    width: 96px!important;
}
.mCSB_scrollTools.mCSB_scrollTools_horizontal {
    width: 636px!important;
    height: 6px;
    top: auto;
    right: 0;
    bottom: 0!important;
    left: 0;
    position: absolute;
    margin: 0 auto;
    @include max (991px){
        width: 400px!important;
    }
    @include max (767px){
        width: 300px!important;
    }
}
.mCSB_scrollTools .mCSB_draggerContainer{
    top: 80px;
    @include max (991px){
        top: 60px;
    }
    @include max (767px){
        top: 50px;
    }
}

// end one



.wiont-slider-init2 ul.slick-dots {
    position: absolute;
    bottom: 0;
    display: flex!important;
    align-items: center;
    gap: 50px;
    left: 50%;
    transform: translate(-50%,-50%);
   
}
.wiont-slider-init2 .slick-dots li.slick-active {
  width: 100px;
  height: 5px;
  border-radius: 50px;
  background-color: var(--heading-bg);
  @include max (767px){
    display: none;
  }
}

.wiont-slider-init2 .slick-dots button {
  display: block;
  width: 100px;
  height: 5px;
  border-radius: 50px;
  background-color: #1D1D1B1A;
  text-indent: -9999px;
  transition: .3s;
  display: flex;
  @include max (767px){
    display: none;
  }
}
// end two


.tabs{
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 24px;
  flex-wrap: wrap;
  padding-bottom: 50px;
  @include max (991px){
    padding-bottom: 40px;
  }
  @include max (767px){
    padding-bottom: 30px;
  }
  li{
    padding: 16px 18px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
    height: 60px;
    background-color: #1D1D1B0D;

    cursor: pointer;
    &.current{
        background-color: var(--white-bg);
    }
  }
}


.wiontab-content{
	display: none;

}

.wiontab-content.current{
	display: inherit;
}
