.wionheader{
  &-section {
    transition: all 0.4s;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: auto;
    z-index: 10;
    text-align: center;
    background-color: var(--white-color);
    @include max (991px){
      padding: 20px 0;
    }
    &.section2{
      padding: 15px 0;
    }
  }
}
header.hide-header {
    transform: translateY(-100%);
}
.wionmain-menu-item{
  text-align: center;
}
.wionheader-section.sticky-menu{
  box-shadow: 0 .125rem .5rem rgba(0,0,0,.04);
  background-color: var(--white-bg);
}

.wionheader{
  &-info-data{
    ul{
      li{
          display: inline-block;
          margin-right: 20px;
          font-size: 16px;
          line-height: 24px;
          font-weight: 600;
          color: var(--white-color);
          position: relative;
          padding-left: 30px;
          &:last-child{
            margin-right: 0;
          }
          img{
            position: absolute;
            top: 2px;
            left: 0;
            
          }
          a{
            color: var(--white-color);
            img{
              position: absolute;
              top: 5px;
              left: 0;
            }
          }
      }
    }
  }
  
}



