.main-menu {
  a {
    color: var(--heading-color);
    font-weight: 500;
    position: relative;
    font-size: 16px;
    display: block;
    letter-spacing: -0.01em;
    transition: all 0.4s;
    text-transform: capitalize;
  }
  &.dark-color{
    a{
      color: var(--heading-color);
    }
  }

  >ul {
    >li {
      margin: 0 14px;
      @include max(1350px) {
        margin: 0 7px;
      }
    }
  }


  ul {
    margin: 0;
    padding: 0;

    li {
      list-style-type: none;
      display: inline-block;
      position: relative;
      &.menu-item-has-children {
        >a {
          &:after {
            content: "\ea4d";
            position: relative;
            margin-left: 2px;
            top: 2px;
            font-size: 20px;
            display: inline-block;
            font-family: 'remixicon';
            transition: all 0.4s ease-in-out;
          }
        }
      }
      &:hover{
        &.menu-item-has-children {
          >a {
            &:after {
              transform: rotate(-180deg);
            }
            
          }
        }
      }
      &:last-child {
        margin-right: 0;
      }

      &:first-child {
        margin-left: 0;
      }

      &:hover {

        >ul.sub-menu {
          visibility: visible;
          opacity: 1;
          z-index: 99;
          pointer-events: visible;
          transform: translateY(0);
        }
      }

    }
  }

  
  ul.sub-menu {
    z-index: 500;
    background-color: #fff;
    box-shadow: -2px 2px 70px -25px rgb(0 0 0 / 30%);
    position: absolute;
    left: 0;
    top: 87px;
    margin: 0;
    padding: 12px 0;
    transition: .4s;
    z-index: 4;
    transform: translatey(20px);
    opacity: 0;
    text-align: left;
    visibility: hidden;
    min-width: 220px;
    border-radius:0 0 6px 6px;
    
    li {
      padding: 5px 10px 5px 20px;
      a {
        font-size: 16px;
        line-height: 30px;
        display: inline-block;
        position: relative;
        font-weight: 400;
        text-transform: capitalize;
        
      }
      &.menu-item-has-children  {
        a.no-border{
          display: block;
          &::before{
            content: none;
          }
          &:after{
            position: absolute;
            right: 0;
            content: "\ea6d";
          }
          
        }
        &:hover a::after{
          transform: rotate(-90deg)!important;
        }
      }
      ul.sub-menu {
        transition: all 0.4s;
        margin-left: 0px!important;
        transform: translateY(30px);
      }
      &:hover ul.sub-menu{
        transform: translateY(0px);
      }
    }
    
  }




  ul.sub-menu {
    padding: 12px 0;
    left: -27px;
    top: 87px;    
    li {
      display: block;
      margin: 0 0;
      transition: all 0.4s;

      ul.sub-menu {
        left: 100%;
        right: auto;
        top: 0;
        margin: 0 0;
        margin-left: 20px;
        border-radius: 10px;
        li {
          ul {
            left: 100%;
            right: auto;
          }
        }
      }
    }
  }
}


ul.sub-menu{
  li{
    a{
      color: var(--heading-color)!important;
      &::before{
        content: '';
        position: absolute;
        bottom: -3px;
        left: auto;
        right: 0;
        height: 2px;
        width: 0;
        background-color: var(--heading-color)!important;
        transition: all 0.3s ease;
        @include max (767px){
          height: 0;
        }
      }
      &:hover::before{
        left: 0;
        right: auto;
        width: 100%;
        
      }
    }
  }
}
a.white-color{
  color: var(--white-color);
}

.menu-style1 {
  >ul {
    >li {
      >a {
        padding: 30px 0;
      }
    }
  }
}




