  text-align: left;
}


// sidebar menu

.wionheader-barger{
  cursor: pointer;
  position: relative;
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 100px;
  border: 1px solid var(--heading-color);
  @include max (767px){
    display: none;
  }
  
  span {
    height: 3px;
    width: 24px;
    margin: 3px 0;
    display: block;
    position: relative;
    background-color: var(--heading-bg);
    &:before {
      content: "";
      top: -8px;
      right: 0;
      width: 24px;
      height: 3px;
      transition: all .4s;
      position: absolute;
      background-color: var(--heading-bg);
    }
    &::after {
      content: "";
      bottom: -8px;
      width: 24px;
      height: 3px;
      right: 0;
      position: absolute;
      transition: all 0.4s;
      background-color: var(--heading-bg);
    }
  }
}



