.wionabout{
    &-content{
        padding-bottom: 100px;
        @include max (767px){
            padding-bottom: 80px;
        }
        @include max (575px){
            padding-bottom: 60px;
        }
    }
    &-content3{
        position: relative;
    }
    // end one
    &-content4{
        position: absolute;
        max-width: 1050px;
        text-align: center;
        top: 25%;
        @include max(767px) {
            padding: 60px 15px;
            position: static;
            opacity: 1 !important;
            transform: scale(1) !important;
        }
    }
    // start inner page

    &-thumb3{
        position: relative;
        text-align: center;
        @include max(991px) {
            transform: rotate(0deg) !important;
        }
        &.box3{
            margin-top: 60px;
            @include max(991px) {
                margin-top: 0;
            }
            @include max(575px) {
                margin-top: 24px;
            }
        }
        
        img{
            border-radius: 10px;
            transform: rotate(-15deg);
            width: 60%;

            @include max(991px) {
                width: 100%;
                transform: rotate(0deg) !important;
            }

        }
        &.thumb4{
            img{
                transform: rotate(15deg);
            }
        }
    }
    &-content2{
        padding-bottom: 60px;
        @include max (991px){
                padding-bottom: 0;
            }
            @include max (767px){
                padding-bottom: 0;
            }
    }
    &-thumb{
        text-align: right;
        img{
            border-radius: 10px;
            @include max(991px) {
                width: 100%;
            }
        }
        @include max (1199px){
            padding-left: 50px;
        }
        @include max (991px){
            text-align: left;
            padding-left: 0;
            padding-bottom: 40px;
        }
        @include max (767px){
            padding-bottom: 30px;
        }
        &.thumb2{
            text-align: left;
            @include max (1199px){
                padding-right: 50px;
            }
            @include max (991px){
                padding-right: 0;
            }
        }
    }
    // start about inner pages
    &-section1{
        padding: 210px 0 130px;
        @include max (991px){
            padding: 170px 0 80px;
        }
        @include max (767px){
            padding: 150px 0 60px;
        }
    }
    &-hero-thumb {
        img{
            border-radius: 10px;
        }
        &.thumb2{
            @include max (767px){
                padding-bottom: 24px;
            }
        }
    }
    &-content3{
        padding: 80px 0 0;
        display: flex;
        flex-direction: column;
        @include max (767px){
            padding: 60px 0 0;
            gap: 5px;
        }
    }
}

.WION_text_invert > div {
    background-image: linear-gradient(to right,  #1d1d1b 50%,  rgba(0, 0, 0, 0.3) 50%);
    padding: 0;
    background-size: 200% 100%;
    background-position: 100%;
    -webkit-background-clip: text;
    background-clip: text;
    color: transparent;
}

.WION_text_invert_2 > div {
    background-image: linear-gradient(to right, #fff 50%, rgba(255, 255, 255, 0.29) 50%);
    padding: 0;
    background-size: 200% 100%;
    background-position: 100%;
    -webkit-background-clip: text;
    background-clip: text;
    color: transparent;
}

.pt-130{
    padding-top: 130px;
    @include max (991px){
        padding-top: 80px;
    }
    @include max (767px){
        padding-top: 60px;
    }
}

.pl-30{
    padding-left: 30px;
    @include max (991px){
        padding-left: 0;
    }
}

.mt-150{
    margin-top: 150px;
    @include max (991px){
        margin-top: 80px;
    }
    @include max (767px){
        margin-top: 60px;
    }
}

.wiontitle {
    font-size: 90px;
    line-height: 1em;
    display: flex;
    gap: 15px;
    align-items: center;
    font-family: $heading-font;
    @include max (1399px){
        font-size: 70px;
    }
    @include max (1199px){
        font-size: 60px;
    }
    @include max (991px){
        font-size: 42px;
    }
    @include max (767px){
        font-size: 36px;
    }
    &.title2{
        color: var(--white-color);
    }
}

.wiontitle-thumb img {
    border-radius: 5px;
    @include max (1199px){
        width: 200px;
    }
    @include max (767px){
        width: 120px;
    }
    @include max (575px){
        display: none;  
    }
}




.wionabout-thumb-slider {
    height: 415px;
}
.swiper-wrapper{
    align-items: center;
    .swiper-slide {
        border-radius: 10px!important;
    }
}







 /* Main Animation Section */
.wionabout-animation-wraper {
    position: relative;
    width: 100%;
    height: 250vh;
    @include max(767px) {
        height: 100%!important;
    }
}

.wionabout-animation-wrap {
    position: sticky;
    top: 0;
    width: 100%;
    height: 80vh;
    overflow: hidden;
    @include max(767px) {
        position: static;
        height: 100%!important;
    }
}
.wionabout-animation-section .pin-spacer{
    height: 8vh!important;
    position: relative;
    @include max(767px) {
        height: 100%!important;
    }
}
.content-wrapper  {
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

/* The animated images */
.animated-image {
    position: absolute;
    width: 350px;
    height: 450px;
    object-fit: cover;
    border-radius: 12px;
    /* All images start at the center */
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    @include max(991px) {
        width: 200px;
        height: 200px;
    }
    @include max(767px) {
        display: none;
    }
}


.wionvideo{
    &-section{
        video{
            width: 100%;
            border-radius: 10px;
        }
    }
    &-wrapper{
        height: 900px;
        overflow: hidden;
    }
    
}

.wionabout-thumb-height{
    height: 600px;
    overflow: hidden;
}