.wionblog{
    &-wrap{
        margin-bottom: 24px;
        transition: all 0.4s;
        &:hover{
            .wionblog-title h3,h4{
                background-size: 100% 2px;
            }
            .wionblog-thumb img{
                transform: scale(1.1);
            }
        }
    }
    &-thumb{
        flex-shrink: 0;
        position: relative;
        overflow: hidden;
        border-radius: 10px;
        img{
            border-radius: 10px;
            object-fit: cover;
            transition: all 0.4s;
            width: 100%;
        }
    }
    &-meta{
        padding: 24px 0;
        ul{
            li{
                display: inline-block;
                a{
                    color: var(--heading-color);
                }
            }
        }
    }
    &-title{
        h3{
            background-image: linear-gradient(currentColor, currentColor);
            background-position: 0 100%;
            background-repeat: no-repeat;
            background-size: 0 1px;
            transition: background-size .3s ease-in-out;
            display: inline;
        }
        @include max (1199px){
            max-width: 70%;
        }
        @include max (991px){
            max-width: 100%;
        }
    }
    &-wraper{
        @include max (1199px){
            display: flex;
            gap: 24px;
        }
         @include max (767px){
            display: block;
        }
    }
    &-wrap2{
        display: flex;
        align-items: center;
        gap: 24px;
        transition: all 0.4s;
        @include max (1199px){
            display: block;
        }
        &.wrap3{
            @include max (991px){
                display: flex;
            }
            @include max (767px){
                display: block;
            }
        }
        &:hover{
            .wionblog-thumb2 img{
                transform: scale(1.1);
            }
            .wionblog-title2 h3{
                background-size: 100% 2px;
            }
            .wionblog-title2 h4{
                background-size: 100% 2px;
            }
        }
    }
    &-thumb2{
        position: relative;
        overflow: hidden;
        border-radius: 10px;
        flex-shrink: 0;
        img{
            object-fit: cover;
            transition: all 0.4s;
            border-radius: 10px;
            @include max (1199px){
                width: 100%;
                height: 414px;
            }
            @include max (991px){
                height: 300px;
            }
        }
    }
    &-title2{
        h3{
            background-image: linear-gradient(currentColor, currentColor);
            background-position: 0 100%;
            background-repeat: no-repeat;
            background-size: 0 1px;
            transition: background-size .3s ease-in-out;
            display: inline;
            @include max (1399px){
                font-size: 33px;
            }
        }
        h4{
            padding-bottom: 0!important;
            background-image: linear-gradient(currentColor, currentColor);
            background-position: 0 100%;
            background-repeat: no-repeat;
            background-size: 0 1px;
            transition: background-size .3s ease-in-out;
            display: inline;
            @include max (1399px){
                font-size: 33px;
            }
        }
        @include max (1199px){
            max-width: 70%;
        }
        @include max (991px){
            max-width: 100%;
        }
    }
    // add single blog
    &-single-thumb{
        img{
            border-radius: 10px;
        }
    }
    &-single-wrap {
        max-width: 1075px;
        @include max (767px){
            max-width: 100%;
        }
    }
    &-single-data{
        h3{
            padding-bottom: 15px;
        }
        p{
            font-weight: 400;
            font-size: 28px;
            line-height: 34px;
            @include max(991px) {
                font-size: 24px;
            }
        }
    }
    &-thumb4{
        position: relative;
        overflow: hidden;
        border-radius: 10px;
        
        img{
            object-fit: cover;
            transition: all 0.4s;
            border-radius: 10px;
            @include max (1199px){
                width: 100%;
                height: 414px;
            }
            @include max (991px){
                height: 300px;
            }
        }
    }
    // blog sidebar
    &-sidebar{
        @include max (991px){
            margin-top: 40px;
            padding-left: 0;
        }
    }
    &-widgets {
        margin-bottom: 50px;
        &:last-child{
            margin-bottom: 0;
        }
        h4{
            padding-bottom: 24px;
        }
        @include max (991px){
            margin-bottom: 30px;
        }
        &.widgets2{
            display: flex;
            align-items: center;
            gap: 24px;
            margin-bottom: 0;
            h4{
                padding-bottom: 0;
            }
        }
    }
    &-categorie{
       ul{
        li{
            margin-bottom: 15px;
            border-bottom: 1px solid #1D1D1B1A;
            padding-bottom: 15px;
            &:last-child{
                margin-bottom: 0;
                border-bottom: none;
                padding-bottom: 0;
            }
            a{
                font-size: 20px;
                line-height: 1.5em;
                transition: all 0.4s!important;
                font-weight: 400;
                color: var(--heading-color);
                &:hover{
                    font-weight: 700;
                }
            }
        }
       }
    }
    &-tags{
        ul{
            display: flex;
            gap: 10px;
            flex-wrap: wrap;
            li{
                display: inline-block;
                a{
                    padding: 10px 20px;
                    display: block;
                    border-radius: 50px;
                    transition: all .4s;
                    color: var(--heading-color);
                    background-color: #1D1D1B0A;
                    &:hover{
                        background-color: var(--accent-bg);
                    }
                }
            }
        }
    }
    &-d-content-wrap{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 30px 0;
        border-top: 1px solid #1D1D1B1A;
        border-bottom: 1px solid #1D1D1B1A;
        margin: 40px 0;
        @include max (767px){
            margin: 30px 0;
            display: block;
        }
    }
    &-d-comment-box{
        h4{
            padding-bottom: 30px;
        }
    }
    &-d-comment-wrap1{
        display: flex;
        &.wrap2{
            padding-bottom: 24px;
            border-bottom: 1px solid #1D1D1B1A;
            margin-bottom: 24px;
        }
    }
    &-d-comment-wrap{
        display: flex;
        padding-bottom: 24px;
        border-bottom: 1px solid #142D6F1A;
        margin-bottom: 30px;
        padding-top: 24px;
        border-top: 1px solid #142D6F1A;
        margin-top: 30px;
        .reply-btn a{
            color: var(--heading-color);
        }
    }
    &-d-comment-thumb{
        margin-right: 20px;
        flex-shrink: 0;
        img{
            border-radius: 100px;
        }
    }
    &-d-comment-data1{
        h5{
            font-size: 20px;
            line-height: 30px;
            font-weight: 500;
            margin-bottom: 5px;
        }
        span{
            font-size: 16px;
            line-height: 22px;
        }
        p{
            margin-top: 15px;
            font-size: 20px;
            line-height: 1.5em;
        }
    }
    &-d-comment-data{
        span{
            font-size: 16px;
            line-height: 22px;
        }
        p{
            color: var(--dark-color1);
            padding-top: 10px;
        }
    }
    &-d-comment-box2{
        h4{
            padding-bottom: 30px;
        }
    }
    &-d-content-comment-box{
        h4{
            margin-bottom: 30px;
        }
    }
}

.reply-btn a{
    color: var(--heading-color);
    transition: all 0.4s;
    &:hover{
        color: var(--accent-color);
    }
}

.tag-share-social ul li a:hover {
    background-color: var(--heading-bg);
}

.tag-share-social{
    display: flex;
    align-items: center;
    gap: 24px;
    ul{
        display: flex;
        gap: 24px;
        flex-wrap: wrap;
        li{
            a{
                width: 35px;
                height: 35px;
                display: flex;
                align-items: center;
                justify-content: center;
                border-radius: 100px;
                transition: all .4s;
                border: 1px solid var(--heading-color);
                &:hover{
                    border: 1px solid transparent;
                    background-color: var(--accent-bg);
                }
            }
        }
    }
}
.wionblockquote-text{
    position: relative;
    padding: 30px;
    border-radius: 10px;
    margin: 40px 0 0;
    background-color: var(--heading-bg);
    @include max (767px){
        margin-top: 30px;
    }
    h4{
        color: var(--white-color);
    }
    &::before {
        top: 0;
        left: 0;
        width: 8px;
        height: 100%;
        content: "";
        position: absolute;
        border-radius: 10px 0 0 10px;
        background: var(--accent-bg);
    }
}
.mb-24{
    margin-bottom: 24px;
}
.mt-30{
    margin-top: 30px;
}
.wiontitle-btn {
    @include max (991px){
        width: 100%;
        text-align: center;
        margin-top: 30px;
    }
}
.pt-40{
    padding-top: 40px;
    @include max (767px){
        padding-top: 30px;
    }
}
.pl-101{
    margin-left: 101px;
    @include max (767px){
        margin-left: 0;
    }
}

.blog-page-wrap{
    padding-right: 50px;
    @include max(991px) {
        padding-right: 0;
    }
}

.wionblog-meta.recent-post-meta{
    padding: 0 0 20px; 
}

.wionrecent-post{
    h4{
        padding-bottom: 0;
    }
    &-item{
        margin-bottom: 24px;
        display: flex;
        gap: 24px;
        @include max(1399px) {
            display: block;
        }
        @include max(991px) {
            display: flex;
            align-items: center;
        }
        @include max(479px) {
            display: block;
        }
        &:last-child{
            margin-bottom: 0;
        }
        &:hover .wionrecent-post-thumb {
            img{
                transform: scale(1.06);
            }
        }
    }
    &-thumb{
        border-radius: 10px;
        overflow: hidden;
        @include max(1399px) {
            margin-bottom: 20px;
        }
        img{
            border-radius: 10px;
            width: 100%;
            height: 100%;
            object-fit: cover;
            transition: all 0.4s;
        }
    }
}