.wionfooter{
  &-textarea{
    @include max (1199px){
      max-width: 60%;
    }
  }
  &-menu-wraper{
    display: flex;
    justify-content: space-between;
    @include max (1199px){
      padding-top: 80px;
    }
    @include max (991px){
      gap: 50px;
      padding-top: 40px;
    }
    @include max (575px){
      padding-top: 30px;
    }
    @include max (479px){
      flex-direction: column;
    }
  }
  &-top{
    padding-top: 100px;
    @include max (991px){
      padding-top: 80px;
    }
    &.top2{
      @include max (767px){
        padding-top: 60px;
      }
    }
  }
  &-menu{
    h4{
      padding-bottom: 24px;
    }
    ul{
      li{
        padding-bottom: 10px;
        &:last-child{
          padding-bottom: 0;
        }
        a{
          color: var(--heading-color);
          display: inline-block;
          position: relative;
          text-transform: capitalize;
          font-weight: 400;
          font-size: 20px;
          line-height: 30px;
          letter-spacing: 0px;
          &::before{
            content: "";
            position: absolute;
            bottom: -3px;
            left: auto;
            right: 0;
            height: 2px;
            width: 0;
            background-color: var(--heading-color) !important;
            transition: all .3s ease;
          }
          &:hover::before{
            left: 0;
            right: auto;
            width: 100%;
          }
        }
      }
    }
    &.menu2{
      @include max (575px){
        padding-bottom: 30px;
      }
    }
  }
  &-menu-wrap{
    display: flex;
    gap: 50px;
  }
  &-column{
    ul{
      li{
        padding-bottom: 10px;
        &:last-child{
          padding-bottom: 0;
        }
        a{
          color: var(--heading-color);
          display: inline-block;
          position: relative;
          text-transform: capitalize;
          font-size: 20px;
          line-height: 30px;
          &::before{
            content: "";
            position: absolute;
            bottom: -3px;
            left: auto;
            right: 0;
            height: 2px;
            width: 0;
            background-color: var(--heading-color) !important;
            transition: all .3s ease;
          }
          &:hover::before{
            left: 0;
            right: auto;
            width: 100%;
          }
        }
      }
    }
  }
  &-bottom-wrap{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 35px 0;
    margin-top: 80px;
    border-top: 1px solid rgba(29, 29, 27, 0.1);
    @include max (991px){
      margin-top: 60px;
    }
    @include max (767px){
      padding: 25px 0;
      margin-top: 40px;
      text-align: center;
      display: block;
    }
  }
  &-logo{
     @include max (767px){
      padding-bottom: 15px;
    }
  }
}

.footer-sticky{
  position: sticky;
  bottom: 0;
  @include max(991px) {
    position: static;
  }
}