.wionhero{
  &-section{
    margin-top: 86.92px;
  }
    
  &-content{
    position: relative;
    padding: 124px 0 0;
    @include max (1199px){
      padding: 100px 0 0;
    }
    @include max (991px){
      padding: 90px 0 0;
    }
    @include max (767px){
      padding: 70px 0 0;
    }
  }
  &-thumb{
    padding: 80px 0 60px 0;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    position: relative;
    z-index: 0;
    img{
      border-radius: 10px;
    }
    @include max (1399px){
      padding: 60px 0 40px 0;
      gap: 24px;
    }
    @include max (991px){
      padding: 60px 0 40px 0;
      grid-template-columns: repeat(2, 1fr);
    }
    @include max (767px){
      padding: 40px 0 40px 0;
      grid-template-columns: repeat(1, 1fr);
    }
    &-item{
      &.item2{
        @include max (991px){
          display: none;
        }
      }
       &.item3{
        @include max (767px){
          display: none;
        }
      }
    }
  }
  // end one
  &-section2{
    position: relative;
    z-index: 0;
    overflow: hidden;
    border-radius: 10px;
    margin-top: 87px;
    padding: 130px 0 100px;
    background-size: cover;
    background-repeat: no-repeat;
    @include max (1199px){
      padding: 110px 0 90px;
    }
    @include max (991px){
      padding: 90px 0 80px;
    }
    @include max (767px){
      padding: 80px 0 60px;
    }
    &::before{
      content: "";
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      position: absolute;
      background: linear-gradient(180deg, rgba(29, 29, 27, 0) 0%, #1D1D1B 100%);
    }
  }
  &-content2{
    position: relative;
    max-width: 1000px;
    text-align: center;
    margin: 0 auto;
    h1{
      color: var(--white-color);
    }
  }
  &-data-wrap{
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 130px;
    @include max (991px){
      padding-top: 80px;
    }
    @include max (767px){
      padding-top: 60px;
      display: block;
      text-align: center;
    }
  }
  &-data-item{
    position: relative;
    max-width: 310px;
    @include max (767px){
      text-align: center;
      max-width: 100%;
    }
    &.item2{
      max-width: 335px;
      text-align: right;
      @include max (767px){
        padding-top: 12px;
        text-align: center;
        max-width: 100%;
      }
    }
    h4{
      color: var(--white-color);
    }
  }
  // end two
  &-section3{
    padding: 210px 0 130px;
     @include max (1199px){
      padding: 190px 0 100px;
    }
    @include max (991px){
      padding: 170px 0 80px;
    }
    @include max (767px){
      padding: 150px 0 80px;
    }
  }
  &-content3{
    text-align: center;
    margin: 0 auto;
    position: relative;
    z-index: 1;
  }
  &-thumb3{
    img{
      border-radius: 10px;
    }
  }
   &-thumb4{
    img{
      border-radius: 10px;
    }
  }
  
}


.animation-container.scroll1 {
  position: absolute;
  top: 0;
  z-index: -1;
  @include max(991px) {
    display: none;
}

}
.animation-container.scroll2 {
  position: absolute;
  bottom: 0;
  right: 0;
  z-index: -1;
  @include max(991px) {
    display: none;
}

}


.hero-thumb1 {
  transform: rotate(-5deg)!important;
  @include max (1399px){
   transform: rotate(0)!important;
  }
}
.hero-thumb3 {
  transform: rotate(5deg)!important;
  @include max (1399px){
   transform: rotate(0)!important;
  }
}
.hero-thumb{
  position: relative;
  z-index: 1;
}





