.wionservice{
    &-wrap{
        position: relative;
        @include max(767px) {
            padding: 24px 0;
            border-bottom: 1px solid rgba(255, 255, 255, 0.1);
        }
        &.service02{
            @include max(767px) {
                border-bottom: 1px solid rgba(29,29,27,.1019607843);
            }
            
        }
    }
    &-item {
        transition: all 0.4s;
        padding-bottom: 24px;
        border-bottom: 1px solid rgba(255, 255, 255, 0.1);
        margin-bottom: 24px;
        position: relative;
        @include max(767px) {
            padding-bottom: 0;
            border-bottom: 0;
        }
        &.item2{
            padding-bottom: 0;
            border-bottom: none;
            margin-bottom: 0;
        }
        &.item3{
            padding-bottom: 24px;
            padding-top: 24px;
            margin-bottom: 0;
            border-bottom: 1px solid #1D1D1B1A;
            &.item4{
                border-bottom: none;
                margin-bottom: 0;
            }
        }
        &:hover{
            .wionservice-title h3{
                color: var(--accent-color);
            }
            .wionservice-icon {
                 span {
                    .arry1 {
                        top: -15px;
                        left: 15px;
                    }
                    .arry2 {
                        bottom: 0;
                        left: 0;
                    }
                }
            }
            .wionservice-wraper.wrapper2{
                border-radius: 10px;
                padding: 24px;
                background-color: var(--heading-bg);
                @include max (767px){
                    padding: 10px;
                }
            }
        }
    }
    &-wraper{
        display: flex;
        transition: all 0.4s;
        align-items: center;
        justify-content: space-between;
    }
    &-title{
        h3{
            color: var(--white-color);
            transition: all 0.4s;
        }
        &.title2{
            h3{
                color: var(--heading-color);
                transition: all 0.4s;
            }
        }
    }
    // strat inner pages
    &-main-box{
        padding: 30px;
        border-radius: 10px;
        background-color: var(--white-color);
    }
    &-d-thumb{
        @include max (991px){
            padding-bottom: 40px;
        }
        @include max (767px){
            padding-bottom: 30px;
        }
        img{
            border-radius: 10px;
        }
    }
    &-d-data{
        ul{
            li{
                position: relative;
                padding-left: 24px;
                margin-bottom: 15px;
                &:last-child{
                    margin-bottom: 0;
                }
                &::before{
                    content: "";
                    position: absolute;
                    width: 10px;
                    height: 10px;
                    top: 10px;
                    left: 0;
                    border-radius: 100px;
                    background-color: var(--heading-bg);
                }
            }
        }
    }
    // start single service
    &-single-wrap {
        max-width: 1075px;
        @include max (767px){
            max-width: 100%;
        }
    }
    &-single-data{
        h3{
            padding-bottom: 15px;
        }
        p{
            font-weight: 400;
            font-size: 28px;
            line-height: 34px;
            @include max(991px) {
                font-size: 24px;
            }
        }
        ul{
             li{
                position: relative;
                padding-left: 24px;
                margin-bottom: 15px;
                &:last-child{
                    margin-bottom: 0;
                }
                &::before{
                    content: "";
                    position: absolute;
                    width: 10px;
                    height: 10px;
                    top: 10px;
                    left: 0;
                    border-radius: 100px;
                    background-color: var(--heading-bg);
                }
                h3{
                    padding-bottom: 0;
                }
            }
        }
    }
    &-single-thumb{
        padding-bottom: 40px;
        @include max (767px){
            padding-bottom: 30px;
        }
        img{
            border-radius: 10px;
        }
    }
}

.light-color{
    color: var(--light-color1);
}

.pl-110{
    padding-left: 110px;
    @include max (1199px){
        padding-left: 50px;
    }
    @include max (991px){
        padding-left: 0;
    }
}
.pr-110{
    padding-right: 110px;
    @include max (1199px){
        padding-right: 50px;
    }
    @include max (991px){
        padding-right: 0;
    }
}


