.wionsection-padding{
    padding: 130px 0;
    @include max (991px){
        padding: 80px 0;
    }
    @include max (767px){
        padding: 60px 0;
    }
    &.pb0{
        @include max (767px){
              padding: 60px 0;
            padding-bottom: 0!important;
        }
    }
    
}
.wionsection-padding2{
    padding: 130px 0 106px;
    @include max (991px){
        padding: 80px 0 56px;
    }
    @include max (767px){
        padding: 60px 0 36px;
    }
    &.section{
        border-radius: 20px;
        background-color: var(--accent-bg);
        h2{
            color: var(--white-color);
        }
    }
}
.wionsection-padding3{
    padding: 0 0 106px;
    @include max (991px){
        padding: 0 0 76px;
    }
    @include max (767px){
        padding: 0 0 36px;
    }
}

.wiondefault-bg{
  border-radius: 10px;
  background-color: var(--light-bg);
  &.bg2{
    background-color: var(--accent-color);
    position: relative;
    z-index: 1;
  }
}
// section title

.wionsection-title{
    padding-bottom: 80px;
    
    &.pb-56{
        padding-bottom: 56px;
        @include max (991px){
            padding-bottom: 36px;
        }
        @include max (767px){
            padding-bottom: 26px;
        }
    }
    @include max (991px){
        padding-bottom: 60px;
        text-align: center;
    }
    @include max (767px){
        padding-bottom: 40px;
    }
    &.max-680{
        max-width: 680px;
    }
    &.max-750{
        max-width: 750px;
    }
    &.title2{
        @include max (991px){
            text-align: left;
        }
    }
    &.center{
        max-width: 650px;
        margin: 0 auto;
        text-align: center;
        &.full-width{
            max-width: 100%;
        }
        &.max-width-700{
            max-width: 700px;
        }
        &.max-w715{
            max-width: 715px;
        }
        &.max-width-750{
            max-width: 750px;
        }
        &.max-width-780{
            max-width: 780px;
        }
        &.max-width-800{
            max-width: 800px;
        }
        &.max-width-1080{
            max-width: 1080px;
        }
        &.width-850{
            max-width: 850px;
            padding-bottom: 80px;
            @include max (991px){
                padding-bottom: 60px;
            }
            @include max (767px){
                padding-bottom: 40px;
            }
        }
        &.max-w100{
            max-width: 100%;
        }
    }
    
}

.wionsub-title{
  padding: 12px 20px;
  border-radius: 50px;
  display: inline-block;
  margin-bottom: 24px;
  background-color: rgba(29, 29, 27, 0.05);
  p{
    font-weight: 700;
    font-size: 18px!important;
    line-height: 1.33em!important;
    text-transform: uppercase;
    @include max (767px){
      font-weight: 500;
    }
  }
  &.title2{
    background-color: rgba(255, 255, 255, 0.05);
    p{
      color: var(--white-color);
    }
  }
  &.title3{
    background-color: var(--white-bg);
  }
}

// default content
.wiondefault-content{
    h2{
        padding-bottom: 20px;
    }
    p{
        font-size: 28px;
        line-height: 1.21em;
        @include max(767px) {
            font-size: 24px;
        }
        @include max(479px) {
            font-size: 20px;
        }
    }
    &.content2{
        @include max (991px){
            padding-bottom: 40px;
        }
       
        h1,h2,h3,h4,p{
            color: var(--white-color);
        }
    }
     &.content3{
        padding-bottom: 80px;
        @include max (991px){
            padding-bottom: 60px;
        }
        @include max (767px){
            padding-bottom: 40px;
        }
        h1,h2,h3,h4,p{
            color: var(--white-color);
        }
    }
    &.team3{
        position: sticky;
        top: 100px;
        @include max (991px){
            padding: 0 0 60px;
        }
        @include max (767px){
            padding: 0 0 40px;
        }
    }
}
.title{
    padding-bottom: 20px;
}
.mt-50{
    margin-top: 50px;
    @include max (991px){
        margin-top: 40px;
    }
    @include max (767px){
        margin-top: 30px;
    }
}
.section{
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}

.pr-50{
    padding-right: 50px;
    @include max (991px){
        padding-right: 0;
    }
}
.pl-50{
    padding-left: 50px;
    @include max (991px){
        padding-left: 50px;
    }
}

.white-speech{
    width: 100%;
    height: 30px;
    background-color: var(--white-bg);
}