.wioncontact{
    &-box{
        padding: 40px 50px;
        border-radius: 10px;
        background-color: var(--white-bg);
        @include max (767px){
            padding: 30px;
        }
    }
    &-title{
        h4{
            padding-bottom: 50px;
            @include max (991px){
                padding-bottom: 40px;
            }
            @include max (767px){
                padding-bottom: 30px;
            }
        }
    }
    &-thumb{
        @include max (991px){
            padding-bottom: 40px;
        }
        @include max (767px){
            padding-bottom: 30px;
        }
        img{
            border-radius: 10px;
            width: 100%;
        }
    }
    &-info-box{
        padding: 24px;
        margin-bottom: 24px;
        border-radius: 10px;
        background-color: var(--white-bg);
        &.box1{
            margin-bottom: 0;
            padding: 24px 24px 60px 24px;
            border-radius: 10px 10px 0 0;
            border-bottom: 1px solid #1D1D1B1A;
        }
        &.box2{
            margin-bottom: 0;
            padding: 60px 24px 60px 24px;
            border-bottom: 1px solid #1D1D1B1A;
            border-radius: 0;
        }
        &.box3{
            margin-bottom: 0;
            padding: 60px 24px 24px 24px;
            border-radius: 0 0 10px 10px;
            transform: rotate(0)!important;
        }

    }
    &-info-icon{
        padding-bottom: 24px;
    }
    &-info-content{
        h4{
            padding-bottom: 15px;
        }
        p{
            font-size: 20px;
            line-height: 1.5em;
            max-width: 350px;
            a{
                font-weight: 700;
                display: block;
                color: var(--heading-color);
            }
        }
    }
    &-map {
        width: 100%;
        height: 500px;
        @include max (991px){
            height: 300px;
        }
    }
}
.mb-00{
    padding-bottom: 106px!important;
    @include max (991px){
        margin-bottom: 82px;
    }
    @include max (767px){
        margin-bottom: 58px;
    }
}