.wionfaq{
  &-item{
    padding-bottom: 30px;
    border-bottom: 1px solid #0006281A;
    margin-bottom: 30px;
    &:last-child{
      padding-bottom: 0;
      border-bottom: none;
      margin-bottom: 0;
    }
    cursor: pointer;
    &.open {
      .wionfaq-body{
        display: block;
        &.body2{
          display: block;
        }
      }
    }
    &.item2{
      border-bottom: 1px solid #FFFFFF1A;
       &:last-child{
        padding-bottom: 0;
        border-bottom: none;
        margin-bottom: 0;
      }
    }
  }
  &-header{
    display: flex;
    gap: 24px;
    align-items: center;
    justify-content: space-between;
    padding-bottom: 15px;
    &.header2{
      padding-bottom: 10px;
      h3{
        color: var(--white-color);
      }
    }
  }
  &-body{
    display: none;
    padding-right: 80px;
    @include max (767px){
      padding-right: 40px;
    }
    p{
      font-size: 28px;
      line-height: 1.21em;
      @include max (767px){
      font-size: 20px;
    }
    }
    &.body2{
      display: none;
      align-items: center;
      padding-right: 0;
      justify-content: space-between;
      p{
        color: var(--white-color);
        padding-right: 150px;
        font-size: 20px;
        margin: 0;
        line-height: 1.5em;
        @include max (1399px){
          padding-right: 130px;
        }
        @include max (991px){
          padding-right: 180px;
        }
        @include max (767px){
          padding-right: 60px;
        }
      }
      &.body3{
        p{
          padding-right: 48%;
          @include max(991px) {
            padding-right: 50px;
          }
        }
      }
    }
  }
  &-data{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.border-b{
  padding-bottom: 0;
  border-bottom: none;
  margin-bottom: 0;
}

.wionactive-icon{
  flex-shrink: 0;
}

.open .wionactive-icon img {
  transform: rotate(45deg);
}

.wionactive-icon img {
  transition: all .4s;
  @include max(767px) {
    width: 24px;
  }
}