.wionsidebar-social{
  ul{
    li{
      display: inline-block;
      margin-right: 15px;
      &:last-child{
        margin-right: 0;
      }
      a{
        width: 35px!important;
        height: 35px;
        display: flex;
        overflow: hidden;
        align-items: center;
        justify-content: center;
        border-radius: 100px;
        position: relative;
        background-color: var(--heading-bg);
        &:hover img.social-hide{
          top: 8px;
        }
        &:hover img.social-show{
          top: -35px;
        }
      }
    }
  }
}
img.social-show{
  top: 10px;
}
img.social-show, 
img.social-hide{
  position: absolute;
  transition: all 0.4s;
}

img.social-hide{
  top: 35px;
}