.wioncta{
    &-section{
        padding: 100px 0;
        position: relative;
        z-index: 9;
        @include max (991px){
            padding: 80px 0;
        }
        @include max (767px){
            padding: 60px 0;
        }
        &.exta-padding{
            padding: 75px 0;
            @include max (991px){
                padding: 50px 0;
            }
            @include max (767px){
                padding: 30px 0;
            }
        }
    }
    &-slider-content{
        display: flex!important;
        white-space: nowrap;
        width: auto!important;
        margin: 0 auto!important;
        h2{
            color: var(--white-color);
        }
    }
    // end one
    &-wrap{
        max-width: 1000px;
        margin: 0 auto;
        text-align: center;
    }
    // end two
    &-wrap3{
        max-width: 1000px;
        text-align: center;
        margin: 0 auto;
        position: relative;
        z-index: 9;
        h2{
            color: var(--white-color);
            span{
                color: var(--accent-color);
            }
        }
    }
    &-btn{
        text-align: center;
    }
}

.animation-container.scroll3 {
    position: absolute;
    top: 0;
    left: -15%;
    z-index: -1;
    @include max(1399px) {
        display: none;
    }
}
.animation-container.scroll4 {
  position: absolute;
  bottom: 0;
  right: -10%;
  z-index: -1;
  @include max(1399px) {
    display: none;
}
}

.wioncta-slider-init2{
    &.slick-initialized.slick-slider {
        direction: rtl;

    }
}
.wioncta-slider-inits {
  height: 100px;
  margin: auto;
  overflow: hidden;
  position: relative;
}

.wioncta-slider-init .slick-list, 
.wioncta-slider-init2 .slick-list{
    overflow: visible;
}


.wioncta-victor-icon {
    padding: 0 40px;
    @include max (1399px){
        padding-bottom: 15px;
    }
    @include max (767px){
        padding-bottom: 5px;
        padding: 0 24px;
        img{
            width: 40px;
        }
    }
}
.fixed-height{
    height: 105px;
    overflow: hidden;
    @include max (1899px){
        height: 90px;
    }
    @include max (991px){
        height: 80px;
    }
    @include max (767px){
        height: 55px;
    }
}