.wionpricing{
    &-wraper{
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        @include max (1199px){
            gap: 24px;
           grid-template-columns: repeat(2, 1fr);
        }
        @include max (767px){
           grid-template-columns: repeat(1, 1fr);
        }
    }
    &-wrap{
        padding: 30px;
        background-color: var(--white-bg);
  
        @include max (991px){
           border-radius: 10px!important;
        }
        &.wrap1{
            border-radius: 10px 0 0 10px;
            border-right: 1px solid #1D1D1B1A;
            @include max (991px){
                border: none;
            }
        }
        &.wrap2{
            border-right: 1px solid #1D1D1B1A;
            @include max (991px){
                border: none;
            }
        }
        &.wrap3{
            border-radius: 0 10px 10px 0;
        }
    }
    &-header{
        p{
            font-size: 20px;
            line-height: 1.5em;
            padding-top: 15px;
        }
    }
    &-body{
        h4{
            padding-bottom: 24px;
        }
        ul{
            li{
                font-size: 20px;
                line-height: 1.5em;
                padding-bottom: 15px;
                position: relative;
                padding-left: 20px;
                &::before{
                    width: 8px;
                    height: 8px;
                    top: 10px;
                    left: 0;
                    content: "";
                    position: absolute;
                    border-radius: 10px;
                    background-color: var(--heading-bg);
                }
                &:last-child{
                    padding-bottom: 0px!important;
                }
            }
        }
    }
    &-body-text{
        padding-top: 24px;
        p{
            position: static;
            padding: 0;
            padding-bottom: 15px;
            margin: 0;
            font-size: 18px;
            line-height: 1.44em;
            span{
                font-weight: 700;
                padding-right: 15px;
            }
        }
    }
}