.wionbrand-slider-js-init2.slick-initialized.slick-slider {
    direction: rtl;
}
.wionbrand-slider{
    &-item{
        padding: 16px 18px;
        display: flex !important;
        align-items: center;
        justify-content: center;
        background: #FFFFFF0D;
        margin: 0 15px;
        height: 60px;
        border-radius: 10px;
        white-space: nowrap;
    }
}
.wionbrand-slider-js-init2{
    margin: 15px 0;
}