.wionteam{
    &-wrap{
        margin-bottom: 24px;
        transition: all 0.4s;
        border-radius: 10px;
        background-color: var(--white-bg);
        &:hover{
            .wionteam{
                &-thumb img{
                    transform: scale(1.1);
                }
            }
        }
    }
    &-thumb{
        position: relative;
        overflow: hidden;
        border-radius: 10px 10px 0 0;
        img{
            width: 100%;
            height: 100%;
            transition: all 0.4s;
            object-fit: cover;
            border-radius: 10px 10px 0 0;
        }
    }
    &-content{
        padding: 30px;
        @include max (767px){
            padding: 18px;
        }
        h4{
            padding-bottom: 10px;
            @include max (767px){
                padding-bottom: 5px;
            }
        }
        p{
            color: var(--heading-color);
        }
        &.content2{
            padding: 30px 13px;
             @include max (767px){
                padding: 18px 12px;
            }
        }
    }
    // end one
    &-wraper{
        display: flex;
        align-items: center;
        gap: 24px;
        @include max (767px){
            display: block;
        }
    }
    &-d-thumb{
        padding-right: 100px;
        img{
            border-radius: 10px;
        }
        @include max (1399px){
            padding-right: 50px;
        }
        @include max (1199px){
            padding-right: 30px;
        }
        @include max (991px){
            padding-bottom: 40px;
            padding-right: 0;
        }
        @include max (767px){
            padding-bottom: 30px;
        }
    }
    &-d-content{
        h3{
            padding-bottom: 15px;
        }
        p{
            font-size: 28px;
            line-height: 34px;
            letter-spacing: 0px;
            @include max(991px) {
                font-size: 24px;
            }
        }
        ul{
            li{
                position: relative;
                padding-left: 24px;
                padding-bottom: 15px;
                &:last-child{
                    padding-bottom: 0;
                }
                &::before{
                    content: "";
                    top: 13px;
                    left: 0;
                    width: 10px;
                    height: 10px;
                    position: absolute;
                    border-radius: 100px;
                    background-color: var(--heading-bg);
                }
            }
        }
    }
}


.team-sticky-top{
    position: sticky;
    top: 100px;
}