.wioniconbox{
    &-wrap {
        padding: 30px;
        border-radius: 10px;
        margin-bottom: 24px;
        background-color: var(--light-bg);
        &:hover .wioniconbox-icon{
            transform: rotateY(1turn);
        }
        &.wrap2{
            background-color: var(--heading-bg);
        }
        @include max (767px){
            padding: 24px;
        }
    }
    &-icon{
        display: inline-block;
        padding-bottom: 100px;
        position: relative;
        transition: transform 1s;
        @include max (991px){
            padding-bottom: 60px;
        }
        @include max (767px){
            padding-bottom: 40px;
        }
        &.icon2{
             padding-bottom: 195px;
            @include max (991px){
                padding-bottom: 130px;
            }
            @include max (767px){
                padding-bottom: 80px;
            }
        }
    }
    &-content{
        h3{
            padding-bottom: 15px;
        }
        p{
            font-size: 20px;
            line-height: 1.5em;
        }
        &.content2{
            h3,p{
                color: var(--white-color);
            }
        }
    }
}